import "./styles.css";
import Web3 from "web3";
import React, { Component } from "react";

class App extends Component {
  componentWillMount() {
    this.loadBlockchainData();
  }

  async loadBlockchainData() {
    var provider =
      "https://rinkeby.infura.io/v3/4dbec6e76f1a42b7ba71e789e4e67e60";
    var web3Provider = new Web3.providers.HttpProvider(provider);
    var web3 = new Web3(web3Provider);

    web3.eth.getBlockNumber().then((block) => {
      console.log("Latest Ethereum Block is ", block);
      this.setState({ block: block });
    });

    //const web3 = new Web3(Web3.givenProvider || "https://rinkeby.infura.io/v3/4dbec6e76f1a42b7ba71e789e4e67e60")

    web3.eth.getAccounts().then((account) => {
      console.log("your account is ", account);
      this.setState({ account: "account" });
    });
  }

  /*
    this.setState({ account: accounts[0] });
  }
  */

  constructor(props) {
    super(props);
    this.state = { account: "", block: "" };
  }

  render() {
    return (
      <div className="container">
        <h1>Hello, World!</h1>
        <p>Your account is: {this.state.account}</p>
        <p>Latest Ethereum Block is: {this.state.block}</p>
      </div>
    );
  }
}

export default App;

